import ipfs from '../ipfs';
import React, { Component } from 'react'

const accepts = [
    { name: "images", acc: "image/*", },
    { name: "audio", acc: "audio/*", },
    { name: "pdf", acc: "application/pdf,application/msword," }

]




export class index extends Component {
    state = {
        file: null,
        ipfsHash: null,
        fileformat: "null",
        fileLoaded: false,
        formatLoaded: false
    }

    captureChange = (event) => {
        console.log("caputreFile")
        event.preventDefault()
        const file = event.target.files[0]
        const reader = new window.FileReader()
        reader.readAsArrayBuffer(file)
        reader.onloadend = () => {
            this.setState({ file: Buffer(reader.result) })
            console.log('buffer', this.state.file)
            console.log(this.setState({ fileformat: "res[0].hash " }))
            console.log('ipfsHash', this.state)
        }
    }

    submit = async () => {


        event.preventDefault()
        console.log("onSubmit")

        ipfs.files.add(this.state.file, async (err, res) => {
            if (err) {
                console.error(err)
                return
            }
            this.setState({ ipfsHash: res[0].hash })
            console.log(res)
            console.log('ipfsHash', this.state.ipfsHash)


        })
    }
    format = async (event) => {
        let value = event.target.value
        let format = accepts.find(a => a.name == value).acc
        if (value == "Choose....") {
            await this.setState(
                {
                    formatLoaded: false
                }
            )
            return
        }

        await this.setState({ 
            fileformat: format ? format : null, 
            formatLoaded:true
            
        })
        console.log(this.state)



    }
    render() {
        return (
            <div>
                <select onChange={this.format}>
                    <option>Choose....</option>
                    {
                        accepts.map(a =>
                            <option>{a.name}</option>
                        )
                    }
                </select><br />
                {this.state.formatLoaded ? <input type="file" multiple onChange={this.captureChange} accept={this.state.fileformat} /> : "Select format"}
                <br />
                {this.state.fileLoaded ? <button onClick={this.submit}>Submit</button> : "Load file"}
                <br />
                {this.state.ipfsHash ? this.state.ipfsHash : "Upload some file"}
                <img src={`https://ipfs.io/ipfs/${this.state.ipfsHash}`} />


            </div>
        )
    }
}

export default index


